.menu-button {
  position: fixed;
  padding: 5px 1vw 10px;
  mix-blend-mode: difference;
  color: #fff;
  cursor: pointer;
  z-index: 1000;
}

.menu-container {
  position: fixed;
  width: 100vw;
  background-color: #000;
  padding: 2vh 0;
  z-index: 10000;
  color: rgb(180, 180, 180);
  transform: translateY(-100%);
  opacity: 0;
  transition: all 0.3s ease-in-out;
}

.menu-container.open {
  transform: translateY(0);
  opacity: 1;
}

@media (max-width: 479px) {
  .menu-button {
    padding: 10px 2vw;
  }
}

.menu-container div:last-child {
  display: flex;
  margin-top: 8vh;
  justify-content: center;
  gap: 7vw;
}

.menu-container p,
.close-button {
  transition: color 0.3s ease;
}

.menu-container p:hover,
.close-button:hover {
  color: #fff;
}